<template>
    <b-container fluid class="content">
        <b-row>
            <b-col>
            <b-card border-variant="light" header="History Push Notification" class="text-center">
                <alert-fail ref="alertFail"> {{ messageError }} </alert-fail>
                <b-card-text>
                    <b-table class="history-table" :items="items" :fields="fields">
                        <template slot="message" slot-scope="row">
                                <a v-b-modal.modal-1 href="#" @click='selectedDetails = row.item.message'>details</a>
                        </template>
                    </b-table>
                      <b-modal size="xl" id="modal-1" title="message" hide-footer>
                        <div class="my-3 text-left">
                            <pre>{{ selectedDetails }}</pre>
                        </div>
                    </b-modal>
                </b-card-text>
            </b-card>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
import AlertFail from '@/components/AlertFail';
const stringUtil = require('../Utils/string-formater');

export default {
  data () {
    return {
      messageError: '',
      items: [],
      fields: ['time', 'success', 'fail', 'platform', 'message'],
      selectedDetails: ''
    };
  },
  components: {
    AlertFail
  },
  mounted () {
    this.getHistory();
  },
  methods: {
    setDefault () {
      this.items = [];
      this.messageError = '';
      this.$refs.alertFail.hide();
    },
    getHistory () {
      this.setDefault();
      const url = process.env.VUE_APP_SERVICE_HOST + '/history/list';
      this.axios.get(url)
        .then(response => {
          const data = response.data;
          if (data) {
            for (let i = 0; i < data.length; i++) {
              let item = {
                time: stringUtil.reformatDate2(data[i].time),
                success: data[i].success,
                fail: data[i].fail,
                platform: data[i].platform,
                message: data[i].message
              };
              this.items.push(item);
            }
          }
        })
        .catch(error => {
          this.$refs.alertFail.show();
          this.messageError = error.message;
        });
    }
  }
};
</script>

<style scoped>
    .content{
        font-size: 0.8em;
    }
    .history-table{
        color: #555555
    }
</style>
